import React from 'react';
import logo from './logo.svg';
import AmountInputContainer from './AmountInputContainer';
import './App.css';


function App() {
  return (
    <div className="App">
      <header className="App-header">
        <a href="https://github.com/marcinant/react-amount"><img src={logo} className="App-logo" alt="logo" /></a>
        <h1 className="App-title">React + redux localized Amount Input</h1>
      </header>
      <p>Amount: </p>
      <AmountInputContainer />
      <AmountInputContainer type="tel" />
    </div>
  );
}

export default App;



